import React, { useState } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '../../components/ui/card';
import { Button } from '../../components/ui/button';
import { Badge } from '../../components/ui/badge';
import { Input } from '../../components/ui/input';
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogTrigger } from '../../components/ui/dialog';
import { Label } from '../../components/ui/label';
import { Textarea } from '../../components/ui/textarea';
import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query';
import { toast } from 'sonner';
import { 
  Building2, 
  Plus, 
  MapPin, 
  Phone, 
  Mail, 
  Users,
  AlertCircle,
  Search,
  Edit,
  Trash2,
  FileText
} from 'lucide-react';
import { 
  getAllClinicalSites, 
  createClinicalSite, 
  updateClinicalSite, 
  deleteClinicalSite 
} from '../../api/services/clinicalSite/clinicalSiteService';

const ClinicalSites = () => {
  const [searchTerm, setSearchTerm] = useState('');
  const [isCreateDialogOpen, setIsCreateDialogOpen] = useState(false);
  const [editingSite, setEditingSite] = useState(null);
  const [formData, setFormData] = useState({
    site_name: '',
    site_code: '',
    description: '',
    address: '',
    city: '',
    state: '',
    country: 'USA',
    postal_code: '',
    phone: '',
    email: '',
    contact_person: ''
  });

  const queryClient = useQueryClient();

  // Fetch clinical sites
  const { data: sitesData, isLoading, error } = useQuery({
    queryKey: ['clinical-sites'],
    queryFn: getAllClinicalSites
  });

  // Create site mutation
  const createMutation = useMutation({
    mutationFn: createClinicalSite,
    onSuccess: () => {
      queryClient.invalidateQueries(['clinical-sites']);
      setIsCreateDialogOpen(false);
      resetForm();
      toast.success('Clinical site created successfully');
    },
    onError: (error) => {
      toast.error('Failed to create clinical site');
      console.error('Create error:', error);
    }
  });

  // Update site mutation
  const updateMutation = useMutation({
    mutationFn: ({ id, data }) => updateClinicalSite(id, data),
    onSuccess: () => {
      queryClient.invalidateQueries(['clinical-sites']);
      setEditingSite(null);
      setIsCreateDialogOpen(false);
      resetForm();
      toast.success('Clinical site updated successfully');
    },
    onError: (error) => {
      toast.error('Failed to update clinical site');
      console.error('Update error:', error);
    }
  });

  // Delete site mutation
  const deleteMutation = useMutation({
    mutationFn: deleteClinicalSite,
    onSuccess: () => {
      queryClient.invalidateQueries(['clinical-sites']);
      toast.success('Clinical site deleted successfully');
    },
    onError: (error) => {
      toast.error('Failed to delete clinical site');
      console.error('Delete error:', error);
    }
  });

  const resetForm = () => {
    setFormData({
      site_name: '',
      site_code: '',
      description: '',
      address: '',
      city: '',
      state: '',
      country: 'USA',
      postal_code: '',
      phone: '',
      email: '',
      contact_person: ''
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (editingSite) {
      updateMutation.mutate({ id: editingSite.id, data: formData });
    } else {
      createMutation.mutate(formData);
    }
  };

  const handleEdit = (site) => {
    setEditingSite(site);
    setFormData({
      site_name: site.site_name || '',
      site_code: site.site_code || '',
      description: site.description || '',
      address: site.address || '',
      city: site.city || '',
      state: site.state || '',
      country: site.country || 'USA',
      postal_code: site.postal_code || '',
      phone: site.phone || '',
      email: site.email || '',
      contact_person: site.contact_person || ''
    });
    setIsCreateDialogOpen(true);
  };

  const handleDelete = (siteId) => {
    if (window.confirm('Are you sure you want to delete this clinical site?')) {
      deleteMutation.mutate(siteId);
    }
  };

  const sites = sitesData?.data || [];
  const filteredSites = sites.filter(site => 
    site.site_name?.toLowerCase().includes(searchTerm.toLowerCase()) ||
    site.site_code?.toLowerCase().includes(searchTerm.toLowerCase())
  );

  if (isLoading) {
    return (
      <div className="p-6 space-y-6">
        <div className="animate-pulse">
          <div className="h-8 bg-gray-200 rounded w-1/4 mb-6"></div>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {[1, 2, 3].map((i) => (
              <div key={i} className="h-64 bg-gray-200 rounded"></div>
            ))}
          </div>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="p-6">
        <div className="text-center py-12">
          <AlertCircle className="h-12 w-12 text-red-400 mx-auto mb-4" />
          <h3 className="text-lg font-semibold text-gray-900 mb-2">Error Loading Sites</h3>
          <p className="text-gray-600 mb-4">Failed to load clinical sites. Please try again.</p>
          <Button onClick={() => queryClient.invalidateQueries(['clinical-sites'])}>
            Retry
          </Button>
        </div>
      </div>
    );
  }

  return (
    <div className="p-6 space-y-6">
      {/* Header */}
      <div className="flex justify-between items-center">
        <div>
          <h1 className="text-3xl font-bold tracking-tight">Clinical Sites</h1>
          <p className="text-muted-foreground">
            Manage research facilities and locations
          </p>
        </div>
        <Dialog open={isCreateDialogOpen} onOpenChange={setIsCreateDialogOpen}>
          <DialogTrigger asChild>
            <Button 
              className="flex items-center gap-2"
              onClick={() => {
                setEditingSite(null);
                resetForm();
              }}
            >
              <Plus className="h-4 w-4" />
              Add New Site
            </Button>
          </DialogTrigger>
          <DialogContent className="max-w-2xl" aria-describedby="site-form-description">
            <DialogHeader>
              <DialogTitle>
                {editingSite ? 'Edit Clinical Site' : 'Create New Clinical Site'}
              </DialogTitle>
              <DialogDescription id="site-form-description">
                {editingSite
                  ? 'Update the clinical site information below. All required fields must be completed.'
                  : 'Fill out the form below to add a new clinical research site. All required fields must be completed.'
                }
              </DialogDescription>
            </DialogHeader>
            <form onSubmit={handleSubmit} className="space-y-4">
              <div className="grid grid-cols-2 gap-4">
                <div>
                  <Label htmlFor="site_name">Site Name *</Label>
                  <Input
                    id="site_name"
                    value={formData.site_name}
                    onChange={(e) => setFormData({...formData, site_name: e.target.value})}
                    required
                  />
                </div>
                <div>
                  <Label htmlFor="site_code">Site Code *</Label>
                  <Input
                    id="site_code"
                    value={formData.site_code}
                    onChange={(e) => setFormData({...formData, site_code: e.target.value})}
                    required
                  />
                </div>
              </div>
              
              <div>
                <Label htmlFor="description">Description</Label>
                <Textarea
                  id="description"
                  value={formData.description}
                  onChange={(e) => setFormData({...formData, description: e.target.value})}
                />
              </div>
              
              <div>
                <Label htmlFor="address">Address</Label>
                <Input
                  id="address"
                  value={formData.address}
                  onChange={(e) => setFormData({...formData, address: e.target.value})}
                />
              </div>
              
              <div className="grid grid-cols-3 gap-4">
                <div>
                  <Label htmlFor="city">City</Label>
                  <Input
                    id="city"
                    value={formData.city}
                    onChange={(e) => setFormData({...formData, city: e.target.value})}
                  />
                </div>
                <div>
                  <Label htmlFor="state">State</Label>
                  <Input
                    id="state"
                    value={formData.state}
                    onChange={(e) => setFormData({...formData, state: e.target.value})}
                  />
                </div>
                <div>
                  <Label htmlFor="postal_code">Postal Code</Label>
                  <Input
                    id="postal_code"
                    value={formData.postal_code}
                    onChange={(e) => setFormData({...formData, postal_code: e.target.value})}
                  />
                </div>
              </div>
              
              <div className="grid grid-cols-2 gap-4">
                <div>
                  <Label htmlFor="phone">Phone</Label>
                  <Input
                    id="phone"
                    value={formData.phone}
                    onChange={(e) => setFormData({...formData, phone: e.target.value})}
                  />
                </div>
                <div>
                  <Label htmlFor="email">Email</Label>
                  <Input
                    id="email"
                    type="email"
                    value={formData.email}
                    onChange={(e) => setFormData({...formData, email: e.target.value})}
                  />
                </div>
              </div>
              
              <div>
                <Label htmlFor="contact_person">Contact Person</Label>
                <Input
                  id="contact_person"
                  value={formData.contact_person}
                  onChange={(e) => setFormData({...formData, contact_person: e.target.value})}
                />
              </div>
              
              <div className="flex justify-end gap-2 pt-4">
                <Button 
                  type="button" 
                  variant="outline" 
                  onClick={() => setIsCreateDialogOpen(false)}
                >
                  Cancel
                </Button>
                <Button 
                  type="submit" 
                  disabled={createMutation.isPending || updateMutation.isPending}
                >
                  {editingSite ? 'Update Site' : 'Create Site'}
                </Button>
              </div>
            </form>
          </DialogContent>
        </Dialog>
      </div>

      {/* Search */}
      <div className="flex items-center gap-4">
        <div className="relative flex-1 max-w-sm">
          <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 h-4 w-4" />
          <Input
            placeholder="Search sites..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            className="pl-10"
          />
        </div>
      </div>

      {/* Sites List */}
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center gap-2">
            <Building2 className="h-5 w-5" />
            Clinical Sites ({filteredSites.length})
          </CardTitle>
        </CardHeader>
        <CardContent>
          {filteredSites.length === 0 ? (
            <div className="text-center py-12">
              <Building2 className="h-12 w-12 text-gray-300 mx-auto mb-4" />
              <h3 className="text-lg font-semibold text-gray-900 mb-2">No Sites Found</h3>
              <p className="text-gray-600 mb-4">
                {searchTerm ? 'No sites match your search criteria.' : 'Get started by creating your first clinical site.'}
              </p>
              {!searchTerm && (
                <Button onClick={() => setIsCreateDialogOpen(true)}>
                  <Plus className="h-4 w-4 mr-2" />
                  Add First Site
                </Button>
              )}
            </div>
          ) : (
            <div className="space-y-4">
              {filteredSites.map((site) => (
                <div key={site.id} className="border rounded-lg p-4 hover:bg-gray-50 transition-colors">
                  <div className="flex items-start justify-between">
                    <div className="flex-1">
                      <div className="flex items-center gap-3 mb-2">
                        <h3 className="font-semibold text-lg">{site.site_name}</h3>
                        <span className="text-sm text-gray-500 bg-gray-100 px-2 py-1 rounded">
                          {site.site_code}
                        </span>
                        <Badge variant="outline" className="bg-green-50 text-green-700 border-green-200">
                          Active
                        </Badge>
                      </div>
                      
                      {site.description && (
                        <p className="text-sm text-gray-600 mb-3">{site.description}</p>
                      )}
                      
                      <div className="grid grid-cols-1 md:grid-cols-2 gap-4 text-sm text-gray-600">
                        {site.address && (
                          <div className="flex items-center gap-2">
                            <MapPin className="h-4 w-4" />
                            {site.address}
                            {site.city && site.state && `, ${site.city}, ${site.state}`}
                          </div>
                        )}
                        {site.phone && (
                          <div className="flex items-center gap-2">
                            <Phone className="h-4 w-4" />
                            {site.phone}
                          </div>
                        )}
                        {site.email && (
                          <div className="flex items-center gap-2">
                            <Mail className="h-4 w-4" />
                            {site.email}
                          </div>
                        )}
                        {site.contact_person && (
                          <div className="flex items-center gap-2">
                            <Users className="h-4 w-4" />
                            Contact: {site.contact_person}
                          </div>
                        )}
                      </div>
                      
                      <div className="mt-3 flex items-center gap-4 text-sm">
                        <span className="flex items-center gap-1">
                          <FileText className="h-4 w-4 text-blue-500" />
                          {site.active_studies || 0} Active Studies
                        </span>
                        <span className="flex items-center gap-1">
                          <Users className="h-4 w-4 text-green-500" />
                          {site.total_participants || 0} Participants
                        </span>
                      </div>
                    </div>
                    
                    <div className="flex gap-2">
                      <Button 
                        variant="outline" 
                        size="sm"
                        onClick={() => handleEdit(site)}
                      >
                        <Edit className="h-4 w-4 mr-1" />
                        Edit
                      </Button>
                      <Button 
                        variant="outline" 
                        size="sm"
                        onClick={() => handleDelete(site.id)}
                        className="text-red-600 hover:text-red-700"
                      >
                        <Trash2 className="h-4 w-4 mr-1" />
                        Delete
                      </Button>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          )}
        </CardContent>
      </Card>
    </div>
  );
};

export default ClinicalSites;
